.wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    * {
        text-shadow: 0px 0px 20px #FFFFFF;
        font-size: 32px;
        color: white;
        font-weight: 900; }
    .secondNameWrapper {
        display: flex;
        justify-content: space-between;
        width: 50%;
        margin-bottom: 50px;
        div {
            width: 25%; } }
    .otherInfoWrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 80%;
        div {
            margin-bottom: 20px; }
        .linksWrapper {
            margin-top: 20vh;
            width: 100%;
            display: flex;
            justify-content: space-between;
            a:hover {
                border-radius: 100px;
                background-color: rgba(255, 255, 255, 0.6);
                transition: 0.2s; } } } }
